/* Common Styles for all Themes */
body {
  font-family: 'Jost', sans-serif;
  transition: background-color 0.5s, color 0.5s;
  margin: 0;
  padding: 0;
  perspective: 1000px;
}

h1, p {
  margin: 10px 0;
}

/* Themes with Depth and Jost Font */
body.theme1, body.theme2, body.theme3, body.theme4, body.theme5, body.theme6, body.theme7, body.theme8, body.theme9, body.theme10 {
  box-shadow: 0 0 50px rgba(0, 0, 0, 0.4);
  color: #fff;
}

/* Theme Styles */
body.theme1 { background: #222; }
body.theme2 { background: #333; }
body.theme3 { background: #191970; }
body.theme4 { background: #708090; }
body.theme5 { background: #004d00; }
body.theme6 { background: #f8f8f8; color: #333; }
body.theme7 { background: #faf0e6; color: #333; }
body.theme8 { background: #afeeee; color: #333; }
body.theme9 { background: #ffe4e1; color: #333; }
body.theme10 { background: #fffff0; color: #333; }

/* Update the App Styles */
.App {
    text-align: center;
    margin-top: 50px;
    position: relative; /* Make the position relative */
  }
  
/* Add this to your existing App.css */

.navbar {
    background: #333;
    padding: 15px;
    text-align: center;
  }
  
  .nav-link {
    margin: 0 15px;
    color: #fff;
    text-decoration: none;
    font-size: 1.2em;
    cursor: pointer;
    transition: color 0.3s ease-in-out;
  }
  
  .nav-link:hover {
    color: #2196F3;
  }
  

  /* Update the button styles */
  button.theme-button {
    padding: 10px 15px; /* Adjust size */
    font-size: 1em; /* Adjust font size */
    cursor: pointer;
    border: 2px solid #fff;
    border-radius: 8px;
    background: linear-gradient(45deg, #4CAF50, #2196F3);
    color: #fff;
    transition: all 0.3s ease-in-out;
    margin-bottom: 40px;
    margin-top: 40px;
  }
  
  button.theme-button:hover {
    transform: scale(1.1);
    background: linear-gradient(45deg, #2196F3, #4CAF50);
    border-color: #2196F3;
  }
  
  /* Optional: Adjust the spacing between the button and the right edge */
  .button-container {
    margin-right: 10px;
  }
  

/* Landing Page Styles */
.landing-page {
  padding: 80px 20px;
  text-align: center;
  transform-style: preserve-3d;
}

/* Header Styles */
header {
    margin-bottom: 40px;
  }
  
  header h1 {
    font-size: 3em;
    color: #2196F3;
    transform: translateZ(50px);
  }
  
  header p {
    font-size: 1.2em;
    color: #555;
    transform: translateZ(30px);
  }
  
/* Enhanced Features Section Styles */
.features {
    position: relative;
    overflow: hidden;
    display: flex;
    flex-wrap: wrap; /* Wrap features to the next row on smaller screens */
    justify-content: space-between;
  }
  
  .feature {
    position: relative;
    z-index: 1;
    transition: transform 0.5s ease-in-out, box-shadow 0.3s ease-in-out;
    width: 30%; /* Adjust as needed */
    margin: 20px;
    background-color: #555; /* Background color for features */
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Optional: Add box shadow for a subtle effect */
  }
  
  .feature:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
    background: linear-gradient(30deg, #ffdb01 0%, #ff851f 30%, #ff3d3d 52%, #ff0f6f 70%, #f0f 100%);
    border-radius: 10px;
    transform-origin: bottom left;
    transform: scaleX(0);
    transition: transform 0.5s ease-in-out;
  }
  
  .feature:hover {
    transform: scale(1.05) rotateX(5deg) rotateY(5deg);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2); /* Add a shadow on hover for depth */
  }
  
  .feature:hover:before {
    transform-origin: top right;
    transform: scaleX(1);
  }
  
  /* Optional: Add animations to text */
  .feature h2, .feature p {
    transition: transform 0.5s ease-in-out;
  }
  
  .feature:hover h2, .feature:hover p {
    transform: translateY(-10px);
  } 
  
  /* Responsive Styles for Smaller Screens */
  @media (max-width: 768px) {
    .features {
      justify-content: center; /* Center features on smaller screens */
    }
  
    .feature {
      width: 100%; /* Full width for individual features on mobile */
      margin-bottom: 20px; /* Add spacing between features */
    }
  
    /* Add more adjustments as needed */
  }
  
footer {
  background: #222;
  color: #fff;
  padding: 20px 0;
  transform: translateZ(10px);
}

footer p {
  margin: 0;
}

.logo {
    width: 50px; /* Set the desired width */
    height: auto; /* Maintain aspect ratio */
    margin-right: 10px; /* Adjust spacing as needed */
  }
  